import {ReduxBehavior} from "./adv-redux-behavior";
import {fetchProductHej} from "../state/product/product-epic-actions";
import {getProductHej, getProductImage} from "../state/product/product-selectors";

class AdvWatchMaker {
    get is() {
        return "adv-watch-maker";
    }

    get behaviors() {
        return [ReduxBehavior];
    }

    get properties() {
        return {
            productImage: {
                type: String,
                statePath: getProductImage
            },

            product: {
                type: String,
                statePath: getProductHej
            }
        };
    }

    ready() {
        this.dispatch(fetchProductHej());
    }
}

Polymer(AdvWatchMaker);



