import {AppState} from "../app-state";
import {createSelector} from "reselect";
import {ProductType} from "./Products";

export const getProducts = (state: AppState) => state.products;

export const getProductHej = createSelector(getProducts, (products) => {
    return products.find(product => product.id === ProductType.HEJ_CHRONO);
});

export const getProductImage = createSelector(getProductHej, (product) => {
    if(product && product.images.edges.length > 0) {
        return product.images.edges[0].node.src;
    }
    return null;
});



