import {combineEpics} from "redux-observable";
import {shopEpics} from "./shop/shop-epics";
import {checkoutEpics} from "./checkout/checkout-epics";
import {productEpics} from "./product/product-epics";

export const appEpics = combineEpics(shopEpics, productEpics, checkoutEpics);


