import {ActionsObservable, combineEpics} from "redux-observable";
import {Action} from "redux-actions";
import {Store} from "redux";
import {AppState} from "../app-state";
import {Client} from "../../services/ShopifyService";
import {FetchProductPayload, PRODUCT_FETCH_REQUEST} from "./product-actions";
import {fetchProductFailure$, fetchProductSuccess} from "./product-epic-actions";

export const fetchProductEpic = (action$: ActionsObservable<Action<FetchProductPayload>>, store: Store<AppState>) => {
    return action$.ofType(PRODUCT_FETCH_REQUEST)
        .switchMap(action => {
            const id = action.payload.id;
            const client = Client.getInstance();
            return client.fetchProduct$(id).map(product => fetchProductSuccess(product))
                .catch(error => fetchProductFailure$(error));
        });
};

export const productEpics = combineEpics(fetchProductEpic);


