import {ReduxBehavior} from "./adv-redux-behavior";
import {getProductImage} from "../state/product/product-selectors";

class AdvHomePage {
    get is() {
        return "adv-home-page";
    }
    get behaviors() {
        return [ReduxBehavior];
    }

    get properties() {
        return {
            productImage: {
                type: String,
                statePath: getProductImage
            }
        };
    }
}

Polymer(AdvHomePage);


