import {Product} from "./product-state";
import {FetchProductSuccessPayload, PRODUCT_FETCH_SUCCESS} from "./product-actions";
import {Action} from "redux-actions";

export function products(state: Product[] = [], action: Action<FetchProductSuccessPayload>): Product[] {
    if (action.type === PRODUCT_FETCH_SUCCESS) {
        const product = action.payload.product;
        return [...state, {...product}];
    }
    return state;
}

