import {Product, UserError} from "../../@types/shopify";
import {createAction} from "redux-actions";
import {of} from "rxjs/observable/of";
import {
    FetchProductFailurePayload,
    FetchProductPayload,
    FetchProductSuccessPayload,
    PRODUCT_FETCH_FAILURE,
    PRODUCT_FETCH_REQUEST,
    PRODUCT_FETCH_SUCCESS
} from "./product-actions";
import {ProductType} from "./Products";



export const fetchProduct = createAction<FetchProductPayload, string>(PRODUCT_FETCH_REQUEST, (id) => ({id}));
export const fetchProductHej = () => fetchProduct(ProductType.HEJ_CHRONO);

export const fetchProductSuccess = createAction<FetchProductSuccessPayload, Product>(PRODUCT_FETCH_SUCCESS, (product) => ({product}));
export const fetchProductSuccess$ = (product: Product) => of(fetchProductSuccess(product));

export const fetchProductFailure = createAction<FetchProductFailurePayload, UserError[]>(PRODUCT_FETCH_FAILURE, (errors) => ({errors}));
export const fetchProductFailure$ = (errors: UserError[]) => of(fetchProductFailure(errors));



