import {createAction} from "redux-actions";

import {of} from "rxjs/observable/of";
import {Shop} from "../../@types/shopify";
import {
    SHOP_FETCH_SUCCESS,
    FetchShopPayload,
    FetchShopSuccessPayload, SHOP_FETCH_FAILURE, SHOP_FETCH_REQUEST
} from "./shop-action-types";

export const fetchShop = createAction<FetchShopPayload>(SHOP_FETCH_REQUEST);

export const fetchShopSuccess = createAction<FetchShopSuccessPayload, Shop>(SHOP_FETCH_SUCCESS, (shop: Shop) => ({shop}));
export const fetchShopSuccess$ = (shop: Shop) => of(fetchShopSuccess(shop));

export const fetchShopFailure = createAction(SHOP_FETCH_FAILURE, (error: Error) => ({error}));
export const fetchShopFailure$ = (error: Error) => of(fetchShopFailure(error));




